/**
 * @description Return audio context or create one if none.
 *              webkitAudioContext object for Safari & old Chrome versions
 * @returns {object} AudioContext
 */
var audioContext;
function getAudioContext() {
    if (audioContext === undefined)
        audioContext = new (window.AudioContext || window.webkitAudioContext)();

    return audioContext;
}

/**
 * @description List of all instanciated AWA components
 * @param ID of the desired component
 * @returns {object} BaseComponent
 */
var components = [];
function getComponent(ID) {
    components.forEach(component => {
        if (component['ID'] == ID) {
            return component;
        }
    });

    throw "No component found with ID = " + ID
}

/**
 * @description The master node is the final node to connect every others components' node before audio output.
 *              Return master gain or create one if none.
 * @returns {object} BaseComponent
 */
var masterNode;
function getMasterNode() {
    if (masterNode === undefined) {
        masterNode = new BaseComponent();
        masterNode.ID = "master"
        masterNode.parents = -1;                        // master node is final node: has no parent
        masterNode.getNode()                            // connect to audio output
            .connect(getAudioContext().destination);
    }

    return masterNode;
}

class BaseComponent {
    constructor() {
        components.push(this);
    }

    /* variables  ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
    ID = undefined;
    node = getAudioContext().createGain();
    parents = [];                              // store parent components ID
    childs = [];                               // store child components ID

    /* functions  ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
    addChild = function (component) {
        this.childs.push(component.ID);
    };
    addParent = function (component) {
        this.parents.push(component.ID);
    };
    connect = function (component) {
        if (component instanceof BaseComponent) {
            component.addChild(this);
            this.addParent(component);
            this.node.connect(component.getNode());
        }
        else {
            this.node.connect(component);
        }
    };
    disconnect = function () {
        //                                          TODO: foreach parent -> disconnect
    }
    getNode = function () {
        return this.node;
    };
}

class playerprototype { audioBuffer = undefined; source = undefined; node = getAudioContext().createGain() }
class PlayerComponent extends BaseComponent { // all players are played simultaneously : --performance ++quality (on source switch)
    constructor() {
        super();
    }

    players = [];
    isInit = false; isPlaying = false;
    updaterFctId = undefined; duration = undefined; remainingDuration = undefined;

    initialize = function (paths, id, callback) {
        this.ID = id;
        paths = (typeof (paths) != "object") ? [paths] : paths; // force list

        let self = this;
        paths.forEach(path => {
            let player = new playerprototype();
            self.players.push(player);

            let request = new XMLHttpRequest();
            request.open('GET', path, true);
            request.responseType = 'arraybuffer';
            request.onload = function () {             // promise of retrieving audio datas
                getAudioContext().decodeAudioData(request.response, (audioBuffer) => {
                    player.audioBuffer = audioBuffer;
                    self.duration = audioBuffer.duration;
                    self.remainingDuration = self.duration;
                    self.isInit = true;
                    if (callback != undefined) callback();
                });
            };
            request.send();
        });

        if (!this.parents.length)
            this.connect(getMasterNode());          // default connection to master
        else {
            //                                      TODO: connect to foreach(parent : parents)
        }
    };

    /**
     * @description Start player playback. Beginning of the playback can be delayed.
     * @param {number} [delayedStart=0]
     */
    play = function (delayedStart) {
        if (this.isPlaying || !this.isInit) return;

        /** if no then 0 */
        delayedStart = delayedStart ? delayedStart : 0;

        /** auto reset */
        if (this.remainingDuration <= 0)
            this.stop()

        this.players.forEach(player => {
            /** initialize source audio buffer */
            player.source = getAudioContext().createBufferSource(); // type: AudioBufferSourceNode, need to recreate source on each playback
            /** get audio data */
            player.source.buffer = player.audioBuffer;
            /** connect to audio node */
            player.source.connect(player.node);
            /** connect to component node */
            player.node.connect(this.node);

            /** start playback at specify time */
            player.source.start(delayedStart, this.duration - this.remainingDuration, this.duration);

        });
        /* updater  ~~~~~~~~~~~~~~~ */
        var self = this;
        var lastTick = getAudioContext().currentTime;
        this.updaterFctId = setInterval(function () {

            /** calcul elapsed time since last callback */
            var tick = getAudioContext().currentTime;
            var delta = tick - lastTick;
            lastTick = tick;
            self.remainingDuration -= delta;
            /** cast playback done event or auto stop */
            if (self.remainingDuration <= 0) {
                self.pause();
                // EventBus.$emit('playerPlaybackEnd@' + self.ID, self.ID);
            }
        }, 50);

        this.isPlaying = !this.isPlaying;
    };
    pause = function () {
        if (!this.isPlaying) return;
        this.isPlaying = !this.isPlaying;

        /** stop playback */
        this.players.forEach(player => {
            player.source.stop();
        });
        /** stop updater */
        clearInterval(this.updaterFctId);
    };
    stop = function () {
        /** stop playback */
        if (this.isPlaying)
            this.players.forEach(player => {
                player.source.stop();
            });
        /** stop updater */
        clearInterval(this.updaterFctId);
        /** reset remaining time */
        this.remainingDuration = this.duration;

        this.isPlaying = false;
    };

    playPause = function () {
        this.isPlaying ? this.pause() : this.play();
    };

    /**
     * @description Mute the choosen player
     *              Mute all player if param is empty
     * @param {number}
     */
    mute = function (index) {
        if (index == undefined)
            this.node.gain.setValueAtTime(0, getAudioContext().currentTime);
        else {
            this.players[index].node.gain.setValueAtTime(0, getAudioContext().currentTime);
        }

    };
    /**
     * @description Unmute the choosen player
     *              Unmute all player if param is empty
     * @param {number}
     */
    unmute = function (index) {
        if (index == undefined)
            this.node.gain.setValueAtTime(1, getAudioContext().currentTime);
        else {
            this.players[index].node.gain.setValueAtTime(1, getAudioContext().currentTime);
        }

    };

    /**
     * @description Set volume of the player
     * @param {number}
     */
    setVolume = function (value) {
        this.node.gain.setValueAtTime(value, getAudioContext().currentTime);
    }

    /**
     * @description Return data from the selected player on the selected channel (left or right)
     * @param {number} [index=0]
     * @param {number} [index=0]
     * @returns {Object}
     */
    getAudioData = function (index, channel) {
        index = (index == undefined) ? 0 : index;
        channel = (channel == undefined) ? 0 : channel;
        if (this.players[index].audioBuffer == undefined) return; // not rdy, abord!
        return this.players[index].audioBuffer.getChannelData(channel);
    };
}


export { getAudioContext, getMasterNode, getComponent, PlayerComponent };