<template>
  <v-row style="height: 100%; width: 100%">
    <v-col cols="6" class="yellow darken-2">
      <v-row align="center" class="my-4">
        <v-col offset="1">
          <span class="headline font-weight-bold">Featured songs</span>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col offset="1">
          <v-list-item class="pa-0 pr-8" two-line>
            <v-btn fab depressed small dark color="#FFFFFF44">
              <v-icon>mdi-rewind</v-icon>
            </v-btn>
            <v-btn class="mx-4" fab depressed dark x-large @click="play()">
              <v-icon v-if="player.isPlaying" large>mdi-pause</v-icon>
              <v-icon v-else large>mdi-play</v-icon>
            </v-btn>
            <v-btn fab depressed small dark color="#FFFFFF44">
              <v-icon>mdi-fast-forward</v-icon>
            </v-btn>

            <v-list-item-content class="ml-12">
              <span class="display-1 font-weight-black">Space-Age Echo</span>
              <span class="title font-weight-medium">J.B. Arthus</span>
            </v-list-item-content>

            <span class="title font-weight-medium">{{ elapsedTime() }}</span>
            <Visualiser class="mx-8" style="height: 7vh; width: 38%"></Visualiser>
            <span class="title font-weight-medium">{{ totalTime() }}</span>
          </v-list-item>
        </v-col>
      </v-row>
    </v-col>
    <v-col cols="2" class="green text-center" style="position: relative">
      <span class="track-number">01</span>
      <div style="position: absolute; top: 0; left: 0; width: 100%">
        <v-icon class="track-icon">mdi-headphones</v-icon>
      </div>
    </v-col>
    <v-col cols="4" class="grey darken-4" style="padding-left: 4vw">
      <v-row align="center" class="my-4">
        <span class="headline font-weight-medium white--text">Next Song</span>
        <v-spacer></v-spacer>
        <v-spacer></v-spacer>
        <v-btn fab x-small class="mr-3">
          <v-icon class="half-large">mdi-chevron-left</v-icon>
        </v-btn>
        <v-btn fab x-small>
          <v-icon class="half-large">mdi-chevron-right</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
      </v-row>
      <v-spacer style="margin-top: 4vh"></v-spacer>
      <v-row align="center">
        <v-list-item class="pa-0" two-line>
          <v-list-item-content>
            <span class="display-1 font-weight-black white--text">Authentic Spring Reverb</span>
            <span class="title font-weight-medium semi-white--text">J.M. Blanchet</span>
          </v-list-item-content>
        </v-list-item>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
import Visualiser from "@/components/Visualiser.vue";
import { PlayerComponent } from "../libs/awa.js";

export default {
  components: { Visualiser },

  data: () => ({
    player: new PlayerComponent()
  }),
  mounted() {
    /** Initialize player (preload file) */
    this.player.initialize("./assets/something.wav", "player");
  },

  methods: {
    play() {
      this.player.playPause();
    },
    elapsedTime() {
      if (!this.player.isInit) return "0:00";
      /** calcul elapsed time */
      var time = this.player.duration - this.player.remainingDuration;
      /** format for display */
      var minutes = Math.floor(time / 59);
      var seconds = time - minutes * 59;
      seconds = seconds.toFixed(0);
      seconds = ("0" + seconds).slice(-2);
      return "" + minutes + ":" + seconds;
    },
    totalTime() {
      if (!this.player.isInit) return "0:00";
      /** calcul elapsed time */
      var time = this.player.duration;
      /** format for display */
      var minutes = Math.floor(time / 59);
      var seconds = time - minutes * 59;
      seconds = seconds.toFixed(0);
      seconds = ("0" + seconds).slice(-2);
      return "" + minutes + ":" + seconds;
    }
  }
};
</script>

<style scoped>
.track-number {
  color: #00000022;
  font-weight: 800;
  font-size: 18vh;
  line-height: 100%;
}
.track-icon {
  color: white;
  font-size: 12vh;
  line-height: 20vh;
}
.semi-white--text {
  color: #ffffff88;
}
</style>