<template>
  <div ref="parent">
    <canvas ref="canvas" />
  </div>
</template>

<script>
import { getAudioContext, getMasterNode } from "@/libs/awa.js";

export default {
  props: {
    color: {
      // optional
      type: String,
      default: "#fff"
    }
  },
  mounted() {
    /* variables ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
    var parentDiv = this.$refs["parent"];
    var parentDivStyle = window.getComputedStyle(parentDiv);

    const canvas = this.$refs["canvas"];
    const ctx = canvas.getContext("2d");
    canvas.width = parseFloat(parentDivStyle.width);
    canvas.height = parseFloat(parentDivStyle.height);
    const analyser = getAudioContext().createAnalyser();
    var master = getMasterNode();
    master.connect(analyser);

    CanvasRenderingContext2D.prototype.roundRect = function(x, y, w, h, r) {
      if (w < 2 * r) r = w / 2;
      if (h < 2 * r) r = h / 2;
      this.beginPath();
      this.moveTo(x + r, y);
      this.arcTo(x + w, y, x + w, y + h, r);
      this.arcTo(x + w, y + h, x, y + h, r);
      this.arcTo(x, y + h, x, y, r);
      this.arcTo(x, y, x + w, y, r);
      this.closePath();
      return this;
    };

    const WIDTH = canvas.width;
    const HEIGHT = canvas.height;

    analyser.fftSize = 512;
    var bufferLength = analyser.frequencyBinCount;
    var dataArray = new Uint8Array(bufferLength);

    ctx.clearRect(0, 0, WIDTH, HEIGHT);

    function draw() {
      requestAnimationFrame(draw);

      analyser.getByteFrequencyData(dataArray);

      ctx.clearRect(0, 0, WIDTH, HEIGHT);

      // var barWidth = (WIDTH / bufferLength) * 2.5;
      var barWidth = 4;
      var barHeight;
      var x = 0;

      for (var i = 20; i < bufferLength; i+=2) {
        barHeight = Math.max(dataArray[i], 8);

        ctx.fillStyle = "rgb(255,255,255)";
        // ctx.fillRect(x, HEIGHT / 2 - barHeight / 4, barWidth, barHeight / 2);
        ctx
          .roundRect(x, HEIGHT / 2 - barHeight / 4, barWidth, barHeight / 2, 90)
          .fill();

        x += barWidth + 12;
      }
    }

    draw();
  }
};
</script>

